<template>
  <v-card
    height="100vh"
    flat
  >
    <v-card
      flat
    >
      <v-card-title class="pb-2">
        {{ $t('Datev') }}
      </v-card-title>

      <v-card-text>
        <v-row>
          <template v-for="(item,index) in datevForm ">
            <v-col
              :key="index"
              cols="12"
              md="4"
              sm="6"
            >
              <v-text-field
                v-model="item.value"
                :label="item.fieldName"
                :placeholder="item.description"
                :rules="item.isRequired === '1' ? [() => !!item.value || 'This field should not be empty'] : []"
              />
            </v-col>
          </template>
          <v-col
            cols="12"
            md="4"
            sm="6"
          >
            <v-btn
              block
              class="green"
              @click="saveConfirm"
            >
              <v-icon left>
                mdi-content-save-outline
              </v-icon>
              {{ $t('保存') }}
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>

      <v-card-text/>
    </v-card>

    <v-spacer/>
    <v-card flat>
      <v-card-text>
        <v-row>
          <v-col cols="6">
            <v-text-field
              label="The previous month's Datev will be sent to the tax preparer on the 1st of each month"
              disabled
            />
            <v-text-field
              :label="sendStatus"
              disabled
            />
          </v-col>
          <v-col cols="6">
            <v-text-field
              :value="emailAddress"
              disabled
              label="E-Mail-Adresse des CSV Empfänger"
            />
            <v-btn
              block
              class="primary pt-13"
              @click="sendDatevToEmail"
            >
              <v-icon left>
                mdi-send
              </v-icon>
              {{ $t('发送') }}
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-card>
</template>

<script>

  import Datev from '../../model/Datev'
  import { formatDate } from '@/common/common'
  import {
    getCurrentCashAmount,
    sendDatevCSVtoReceiver,
    showAllDatevSettings,
    updateDatevSettings,
  } from '@/components/Utlis/api'
  import dayjs from 'dayjs'
  import Utils from '@/components/Utlis/Utils'
  // import { IkDataTable } from 'metaflow-js'

  // const today = dayjs().format('YYYY-MM-DD')
  // const todayDateRange = [today, today]
  export default {
    name: 'Datev',
    // components: { IkDataTable },
    data: function () {
      return ({
        modelDatevTime: Datev,
        datevForm: null,
        emailAddress: null,
        sendStatus: null,
        DateFormLabel: ['E-Mail:',
                        'Beratenummer:',
                        'Mandantennummer:',
                        'Datum: (MMTT)',
                        'Kleinste Sachkontennummemlänge:',
                        'Name:',
                        'Verarbeitungskennzeichen:',
                        'Konto Für 7% USt:',
                        'Konto Für 19% USt:',
                        'Konto Für 0% USt:',
        ],

      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return {
          start: this.dates[0],
          end: this.dates[1],
        }
      },

      filterCashBook () {
        if (Date.parse(this.datesCashBook[0]) > Date.parse(this.datesCashBook[1])) {
          [this.datesCashBook[0], this.datesCashBook[1]] = [this.datesCashBook[1], this.datesCashBook[0]]
        }
        return {
          fromDate: this.datesCashBook[0],
          toDate: this.datesCashBook[1],
        }
      },

      // filter () {},
    },
    watch: {},

    async mounted () {
      // this.dates = [formatDate(0), formatDate(0)]
      this.datesCashBook = [dayjs().format('YYYY-MM-DD'), formatDate(0)]
      this.currentAmount = await getCurrentCashAmount()
      // this.datevForm = await this.getDatevSettings()
      await this.initialize()
      // console.log('todayDateRange', todayDateRange)
    },
    methods: {
      async sendDatevToEmail () {
        Utils.showLoading(true)
        const res = await sendDatevCSVtoReceiver()
        this.sendStatus = res.content.length <= 0 ? 'Sent sucessfully!'.toUpperCase() : res.content[0].toUpperCase()

        if (res.content.length <= 0) {
          // Utils.showConfirm(this.sendStatus, '')
          Utils.hideLoading(this.sendStatus)
        } else {
          Utils.showError(this.sendStatus, '')
        }

        return false
      },

      saveConfirm () {
        let hasEmptyItem = false

        if (this.datevForm.find(_ => _.isRequired === '1' && _.value === '')) {
          hasEmptyItem = true
          Utils.showError('some field is empty')
          return false
        }

        // eslint-disable-next-line no-return-assign
        // this.datevForm.forEach(_ => _.value = "'" + _.value + "'")

        if (!hasEmptyItem) {
          console.log('hasEmptyItem', hasEmptyItem)
          updateDatevSettings(this.datevForm).then(() => {
            Utils.toast('提交成功')
            this.initialize()
          })
        }
      },

      async initialize () {
        this.datevForm = await this.getDatevSettings()
        this.emailAddress = this.datevForm.find(_ => _.key === 'sendingToEmail').value
      },

      clear () {
        this.dates = [formatDate(0), formatDate(0)]
        this.datesCashBook = [formatDate(0), formatDate(0)]
      },
      calculateTotalPrice (items) {
        return parseFloat(this.calculateIncomePrice(items)) - parseFloat(this.calculateOutcomePrice(items))
      },
      calculateIncomePrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.type !== 2 ? arr + parseFloat(item.amount) : arr
          , 0) : 0
      },
      calculateOutcomePrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.type !== 1 ? arr + parseFloat(item.amount) : arr
          , 0) : 0
      },

      async getDatevSettings () {
        const _data = await showAllDatevSettings().then(res => res.content)
        // this.datevForm = _data
        return _data
      },

    },
  }

</script>
