export function formatDate (num) {
  const now = new Date()
  const nowTime = now.getTime()
  const oneDayTime = 24 * 60 * 60 * 1000
  const ShowTime = nowTime + num * oneDayTime
  const myDate = new Date(ShowTime)
  const y = myDate.getFullYear()
  const m = myDate.getMonth() + 1
  const d = myDate.getDate()
  return [y, m, d].join('-')
}

export function formatFullData (num) {
  const now = new Date()
  const nowTime = now.getTime()
  const oneDayTime = 24 * 60 * 60 * 1000
  const ShowTime = nowTime + num * oneDayTime
  const myDate = new Date(ShowTime)
  const y = myDate.getFullYear() + '-'
  const m = myDate.getMonth() + 1
  const d = myDate.getDate()
  const h = myDate.getHours() < 10 ? '0' + myDate.getHours() : myDate.getHours()
  const mm = myDate.getMinutes() < 10 ? '0' + myDate.getMinutes() : myDate.getMinutes()
  const s = myDate.getSeconds() < 10 ? '0' + myDate.getSeconds() : myDate.getSeconds()
  return y + m + '-' + d + ' ' + h + ':' + mm + ':' + s
}

export function rundTimeSecondToMin (item) {
  const temp = item
  if (temp.maxDineTime) temp.maxDineTime = temp.maxDineTime / 60
  if (temp.roundTime) temp.roundTime = temp.roundTime / 60
  // if(item.)
  return temp
}
